import Breadcreumb from "./components/Breadcreumb.js";
import Nodes from "./nodes/Nodes.js";
import { request } from "./api.js";
import ImageView from "./components/ImageView.js";
import Loading from "./components/Loading.js";

const cache = {};

class App {
  constructor($app) {
    this.$app = $app;
    // root,  node,  
    this.state = {
      isRoot: true,
      nodes: [],
      depth: [],
      selectedFilePath: null, //   
      isVisible: false,
      isLoading: true,
    };

    this.loading = new Loading({ $app, initialState: this.state.isLoading });

    //     
    this.breadcreumb = new Breadcreumb({
      $app,
      initialState: this.state.depth,
    });

    //   
    this.imageview = new ImageView({
      $app,
      initialState: {
        selectedFilePath: this.state.selectedFilePath,
        isVisible: this.state.isVisible,
      },
    });

    //  
    this.nodes = new Nodes({
      $app,
      initialState: {
        isRoot: this.state.isRoot,
        nodes: this.state.nodes,
      },
      onClick: async (node) => {
        try {
          this.loading.setState({ ...this.state, isLoading: true });
          if (node.type === "DIRECTORY") {
            if (cache[node.id]) {
              this.setState({
                ...this.state,
                isRoot: false,
                depth: [...this.state.depth, node],
                nodes: cache[node.id],
                isVisible: false,
              });
            } else {
              // 
              const nextNodes = await request(node.id);
              this.setState({
                ...this.state,
                isRoot: false,
                nodes: nextNodes,
                depth: [...this.state.depth, node],
                isVisible: false,
              });
              cache[node.id] = nextNodes;
            }
          } else if (node.type === "FILE") {
            // 
            this.setState({
              ...this.state,
              selectedFilePath: node.filePath,
              isVisible: true,
            });
          }
        } catch (e) {
          throw new Error(e);
        } finally {
          this.setState({
            ...this.state,
            isLoading: false,
          });
        }
      }, //       onclick    
      onBackClick: async () => {
        try {
          this.loading.setState({ ...this.state, isLoading: true });
          // state  
          const prevState = { ...this.state };
          prevState.depth.pop();

          const prevNodeId = prevState.depth.length === 0 ? null : prevState.depth[prevState.depth.length - 1].id;
          //Root   , Root 
          if (prevNodeId == null) {
            const rootNodes = (await cache.root) || request();
            this.setState({
              ...prevState,
              isRoot: true,
              nodes: rootNodes,
              isVisible: false,
            });
          } else {
            const prevNodes = (await cache[prevNodeId]) || request(prevNodeId);
            this.setState({
              ...prevState,
              isRoot: false,
              nodes: prevNodes,
              isVisible: false,
            });
          }
        } catch (e) {
          throw new Error(e);
        } finally {
          this.setState({
            ...this.state,
            isLoading: false,
          });
        }
      }, //     root  
    });

    this.init();
  }

  //    (   node     )
  setState = (nextState) => {
    this.state = nextState;
    this.breadcreumb.setState(this.state.depth);
    this.nodes.setState({
      isRoot: this.state.isRoot,
      nodes: this.state.nodes,
    });
    this.imageview.setState({
      selectedFilePath: this.state.selectedFilePath,
      isVisible: this.state.isVisible,
    });
    this.loading.setState(this.state.isLoading);
  };

  //api    
  init = async () => {
    try {
      const rootNodes = await request(); // api   request 
      this.setState({
        ...this.state,
        isRoot: true, // root  true 
        nodes: rootNodes, // api   
        depth: [], //root     
        selectedFilePath: null,
        isVisible: false,
      });
      cache.root = rootNodes;
    } catch (e) {
      throw new Error(e);
    } finally {
      this.setState({
        ...this.state,
        isLoading: false,
      });
    }
  };
}

export default App;